import React from "react";
import PropTypes from "prop-types";

const FormImg = ({handleSubmit}) => {
    return (
        <>
            <form
                onSubmit={handleSubmit}
                className="d-flex justify-content-evenly align-items-end"
            >
                <label className="w-75 m-1">
                    Filter by:{" "}
                    <input className="w-100" type="text" name="inputTexto" />
                </label>
                <button type="submit" className="btn btn-warning m-1">
                    search
                </button>
            </form>
        </>
    );
};

FormImg.propTypes = {handleSubmit:PropTypes.func};

export default FormImg;
