import PropTypes from "prop-types";

const Card = ({ img, alt }) => {
    return (
        <div className="card m-2" style={{ width: "20rem" }}>
            <img src={img} className="card-img-top" alt={alt} />
        </div>
    );
};

Card.propTypes = {
    img: PropTypes.string,
    alt: PropTypes.string,
};

export default Card;