import { useCallback, useEffect, useState } from "react";

export const useCards = () => {
    const [images, setImages] = useState([
        {
            id: "1",
            alt_description: "placeholder",
            urls: { regular: "https://via.placeholder.com/150" },
        },
    ]);

    const [input, setInput] = useState("");
    const [loading, setLoading] = useState(false);

    const peticion = useCallback(async () => {
        setLoading(true);
        const key = "client_id=-7yMUvK_mwVGFQ5fFzvbinLbP0lkmMnddSR1b5ULnnU";
        const route = input
            ? `https://api.unsplash.com/search/photos/?per_page=5&query=${encodeURI(
                  input
              )}&${key}`
            : `https://api.unsplash.com/photos/?${key}`;
        const res = await fetch(route);
        const data = await res.json();
        if (data.results) setImages(data.results);
        else setImages(data);
        setLoading(false);
    }, [input]);

    useEffect(() => {
        peticion();
    }, [peticion]);

    const handleSubmit = (e) => {
        e.preventDefault();
        const text = e.target[0].value;
        setInput(text);
        e.target[0].value = "";
    };

    return [handleSubmit, images, loading];
};
